header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA Styling */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* HeaderSocial Styling */

.header__socials::before {
    content: "";
    width: 1px;
    height: 5rem;
    background: var(--color-primary);
}


.header__socials {
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 5rem;
    background: var(--color-primary);
}

.header__socials a:hover {
    color: purple;
    font-size: 4rem;
}

/* avi styling */

.avi {
    background: linear-gradient(var(--color-bg2), transparent);
    width: 28rem;
    height: 35rem;
    position: absolute;
    left: calc(50% - 14rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* avi styling */

.scroll__down {
    position: absolute;
    right: -3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.2rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
header {
    height: 100vh;
}
}

    /* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }  

    .header__socials,
    .scroll__down
    {
        display: none;
    }
}